nav.main-nav {
	width: 100%;
	position: fixed;
	top: 0;
	box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
	ul {
		padding: 0;
		list-style: none;
		margin: 0;
		li {
			height: 2rem;
			line-height: 2rem;
			display: inline-block;
			width: 20%;
			float: left;
			text-align: center;
			background: $dark-grey;
			border-right: 2px solid $white;
			&:last-child {
				border-right: none; }
			a {
				color: $white;
				text-decoration: none; } } }
	@media screen and (max-width: $break-medium) {
		position: relative;
		ul li {
			width: 100%; } } }
